<template>
  <div id="app">
    <div class="header">
      <p class="titleP"
         @click="jumpCountryTabs">{{ $t("title") }}</p>
    </div>
    <div class="content">
      <!-- 上次使用的链接 -->
      <div class="recently-used"
           v-if="recentlyUsed && !extInfo.connection">
        {{ $t("recent_use") }}: <span>{{ recentlyUsed.name }} </span>
        <span v-if="connecting">{{ $t("connecting") }}</span>
        <span v-else-if="isEdgePlatform()&&connectRest==0"
              @click="openGithubDesktopRelease"
              style="text-decoration: underline; cursor: pointer;color:#f1cd42;">{{$t("reached_download")}}</span>
        <span v-else
              @click="reconnect"
              style="text-decoration: underline; cursor: pointer;">{{ reconnectText }}</span>
      </div>
      <p class="link_type">
        <svg v-if="extInfo.connection"
             class="svg-icon"
             height="100"
             style="fill: rgb(124, 180, 97);margin:0 auto;"
             viewBox="0 0 512 512"
             width="100"
             xmlns="http://www.w3.org/2000/svg">
          <path d="M173.898 439.404l-166.4-166.4c-9.997-9.997-9.997-26.206 0-36.204l36.203-36.204c9.997-9.998 26.207-9.998 36.204 0L192 312.69 432.095 72.596c9.997-9.997 26.207-9.997 36.204 0l36.203 36.204c9.997 9.997 9.997 26.206 0 36.204l-294.4 294.401c-9.998 9.997-26.207 9.997-36.204-.001z"></path>
        </svg>
        <img v-else
             alt=""
             src="../assets/picture1.png">
      </p>
      <p class="online_users">{{ $t("users_num") }} {{ users }}</p>
      <p class="connection_VPN">
        <span v-if="extInfo.connection"
              style="color: rgb(244,161,0);">{{ $t("else_time") + count }}</span>
        <span v-if="!extInfo.connection"
              style="color:#C0C4CC;">{{ $t("vpn_disconnect") }}</span>
      </p>
      <p class="select_button">
        <button v-if="!extInfo.connection"
                class="connection_button"
                @click="jumpCountryTabs">{{ $t("connect") }}</button>
        <button v-if="extInfo.connection"
                class="disconnect_button"
                @click="disconnect">{{ $t("disconnect") }}</button>
        <button style="font-weight: 700;color:#000"
                @click="openGithubDesktopRelease">
          <svg style="margin-left:4px;margin-right: 5px;width:20px"
               t="1632813963499"
               class="icon"
               viewBox="0 0 1024 1024"
               version="1.1"
               xmlns="http://www.w3.org/2000/svg"
               p-id="4325"
               width="32"
               height="32">
            <path d="M707.84 384H640V170.666667c0-23.466667-19.2-42.666667-42.666667-42.666667h-170.666666c-23.466667 0-42.666667 19.2-42.666667 42.666667v213.333333H316.16c-37.973333 0-57.173333 46.08-30.293333 72.96l195.84 195.84c16.64 16.64 43.52 16.64 60.16 0l195.84-195.84c26.88-26.88 8.106667-72.96-29.866667-72.96zM213.333333 810.666667c0 23.466667 19.2 42.666667 42.666667 42.666666h512c23.466667 0 42.666667-19.2 42.666667-42.666666s-19.2-42.666667-42.666667-42.666667H256c-23.466667 0-42.666667 19.2-42.666667 42.666667z"
                  fill="#000000"
                  p-id="4326"></path>
          </svg>
          {{ $t("downloadPc2") }}
        </button>
      </p>
      <div class="node_country"
           style="height: 30px;">
        <p>· {{ $t("bt.a") }}</p>
        <p>· {{ $t("bt.b") }}</p>
        <p>· {{ $t("bt.c") }}</p>
        <p>· {{ $t("bt.d") }}</p>
      </div>
      <div v-if="isEdgePlatform()"
           class="connect-tip">{{$t("limited_time_tip01")+connectMaxMinutes+$t("limited_time_tip02")}}</div>
      <br>
    </div>
  </div>

</template>

<script>
import axios from "axios"
export default {
  name: "App",
  data() {
    return {
      extInfo: {
        version: "0.9",
        uid: "",
        connection: null,
        sharedDate: null,
      },
      recentlyUsed: null,
      connecting: false,
      reconnectText: "立即连接",
      users: "...", //在线人数
      count: "", //倒计时
      seconds: 1800, // 剩余的秒数
      ghDesktopDownloadPage: "https://greenhubtx.ga",
      connectMaxMinutes: 60,
      connectEveryMinutes: 30,
      connectRest: 0,
    }
  },
  async created() {
    if (this.isEdgePlatform()) {
      this.getCurRestTime()
    }
    await this.getExtInfo()
    this.getTotalConnections().then()
    const self = this
    axios
      .get(
        `https://static.greenhubtx.ga/ghex/config.json?t=${new Date().getHours()}&uid=${
          this.extInfo.uid
        }&version=${this.extInfo.version}`
      )
      .then(async (response) => {
        let data = response.data
        self.ghDesktopDownloadPage = `${data.gh_desktop_download_page}?utm_source=gh&utm_medium=click&utm_campaign=popup`

        if (self.isEdgePlatform()) {
          if (data.gh_edge_connect_max_time) {
            self.connectMaxMinutes = data.gh_edge_connect_max_time
          }
          if (data.gh_edge_connect_time) {
            self.connectEveryMinutes = data.gh_edge_connect_time
          }
          await browser.storage.local.set({
            connectMaxMinutes: self.connectMaxMinutes,
            connectEveryMinutes: self.connectEveryMinutes,
          })
        } else {
          if (data.gh_edge_connect_time) {
            self.connectEveryMinutes = data.gh_ce_connect_time
          }
          await browser.storage.local.set({
            connectEveryMinutes: data.gh_ce_connect_time,
          })
        }

        if (!this.extinfo.connection) {
          //connection不存在，说明当前为未连接状态，需要重置seconds，连接时不能重置
          self.seconds = self.connectEveryMinutes * 60
        }
      })
    this.$ga.pageview("/popup").send()
    this.$ga.event("popup/open", "open").send()
  },

  methods: {
    //  分 秒 格式化函数
    countDown() {
      let m = parseInt((this.seconds / 60) % 60)
      m = m < 10 ? "0" + m : m
      let s = parseInt(this.seconds % 60)
      s = s < 10 ? "0" + s : s
      this.count = m + ":" + s
    },
    //定时器每过1秒参数减1
    Time() {
      this.timer = setInterval(() => {
        this.seconds -= 1
        this.countDown()
        if (this.isEdgePlatform()) {
          this.getCurRestTime()
        }
      }, 1000)
    },
    async getCurRestTime() {
      //上次使用日期
      let lastUseData = await browser.storage.local.get({ date: "" })

      //今天日期
      let today = new Date()
      let curDate = `${today.getFullYear()}-${
        today.getMonth() + 1
      }-${today.getDate()}`
      if (lastUseData && lastUseData.date && lastUseData.date == curDate) {
        //获取剩余使用时间
        let restTime = await browser.storage.local.get({
          connectRest: 60,
        })
        this.connectRest = restTime.connectRest
        if (this.connectRest == 0) {
          this.disconnect()
        }
      } else {
        let connectTimeData = await browser.storage.local.get({
          connectMaxMinutes: 60,
        })
        this.connectRest = connectTimeData.connectMaxMinutes
        await browser.storage.local.set({
          date: curDate,
          connectRest: this.connectRest,
        })
      }
    },
    // 打开商店页面
    openChromeStorePage() {
      var homePageUrl = process.env.VUE_APP_HOMEPAGE_URL
      window.open(homePageUrl)
    },
    openGithubDesktopRelease() {
      window.open(this.ghDesktopDownloadPage)
      this.$ga.event("Popup_downloadButton_click_count", "click").send()
      this.$ga.event("click/githubdesktop", "releases").send()
    },
    // 获取插件基本信息
    async getExtInfo() {
      let resp = await browser.runtime.sendMessage({
        action: "extInfo",
        data: {},
      })
      this.extInfo = {
        ...this.extInfo,
        ...resp,
      }
      if (this.extInfo.connection) {
        //获取连接时候的时间戳，用现在的时间戳减去连接的时间戳，计算出现在已经连接的时间
        this.seconds =
          (this.connectEveryMinutes * 60 * 1000 -
            (new Date().getTime() - this.extInfo.connection.timestamp)) /
          1000
        this.countDown()
        this.Time()
      }

      this.recentlyUsed = await browser.runtime.sendMessage({
        action: "lastConnected",
        data: {},
      })
      console.debug(
        "getExtInfo: getting basic information: ",
        this.extInfo,
        this.recentlyUsed
      )
    },
    async getTotalConnections() {
      this.users = await browser.runtime.sendMessage({
        action: "getTotalConnections",
        data: {},
      })
    },
    async reconnect() {
      this.connecting = true
      const rst = await browser.runtime.sendMessage({
        action: "reconnect",
        data: {},
      })
      this.$ga.event("popup/reconnect", this.recentlyUsed.name).send()
      this.connecting = false
      this.getExtInfo().then()
      if (rst) {
        this.reconnectText = ""
      } else {
        this.reconnectText = "重试"
      }
      this.jumpCountryTabs()
    },
    jumpCountryTabs() {
      window.open(chrome.runtime.getURL("options.html"))
    },
    async disconnect() {
      await browser.runtime.sendMessage({
        action: "disconnect",
        data: {},
      })
      await this.getExtInfo()
      this.reconnectText = "立即连接"
      clearInterval(this.timer)
    },
    //判断edge浏览器
    isEdgePlatform() {
      return process.env.VUE_APP_TYPE == "GH_EDGE"
    },
    defferScroll: function (event) {
      event.preventDefault()
    },
  },
  mounted() {
    //禁止页面滚动
    document.body.addEventListener("touchmove", this.defferScroll, {
      passive: false,
    })
    document.body.addEventListener("wheel", this.defferScroll, {
      passive: false,
    })
  },
  beforeDestroy() {
    clearInterval(this.timer)
    this.timer = null
  },
}
</script>

<style>
@import "../css/main.css";
html {
  width: 334px;
  height: 620px;
}

#app {
  width: 334px;
  height: 620px;
  display: flex;
  flex-direction: column;
}

.recently-used {
  font-size: 12px;
  color: #81b667;
  margin-left: 1em;
  font-family: "SourceHanSansSC-regular";
}
</style>
